import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { Chat } from '@progress/kendo-react-conversational-ui';
import '@progress/kendo-theme-default/dist/all.css';
import './App.css';

const initialMessages = [
  {
    author: { id: 1, avatarUrl: './bot.jpg' },
    text: 'Bonjour, comment puis-je vous aider aujourd\'hui?',
    timestamp: new Date(),
  },
];

const App = () => {
  const [messages, setMessages] = useState(initialMessages);
  const messagesEndRef = useRef(null);

  const user = { id: 2, avatarUrl: './user2-160x160.jpg' };

  const handleSendMessage = (event) => {
    const newMessage = {
      author: user,
      text: event.message.text,
      timestamp: new Date(),
    };

    setMessages((prevMessages) => [...prevMessages, newMessage]);

    // Send message to Django via Axios POST
    axios.post('http://127.0.0.1:8000/conversation_doc/', { message: event.message.text })
      .then(response => {
        const botReply = {
          author: { id: 1, avatarUrl: './bot.jpg' },
          text: response.data.reply,  // Assuming your Django API returns { reply: 'response text' }
          timestamp: new Date(),
        };
        setMessages((prevMessages) => [...prevMessages, botReply]);
      })
      .catch(error => {
        console.error('Error sending message to Django:', error);
      });
  };

  const scrollToBottom = () => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  return (
    <div style={styles.container}>
      <Chat
        messages={messages}
        user={user}
        onMessageSend={handleSendMessage}
        placeholder="Entrez votre message..."
      />
      <div ref={messagesEndRef} />
    </div>
  );
};

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100vh',
    backgroundColor: '#f0f0f0',
    overflow: 'auto',
  },
};

export default App;
